.cardContainer {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	padding: 24px;
	width: 450px;
	background-color: #fff;
	border: 1px solid #eee;
	box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.37);
	border-radius: 2px;
	margin-bottom: 32px;
}

.userInfo {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.listItem {
	display: flex;
	flex-direction: column;
}

@media only screen and (max-width: 650px) {
	.cardContainer {
		flex-wrap: wrap;
		width: 100%;
		height: 100%;
		flex-direction: column;
	}
}
